.show-bearer-panel {
    display: flex;
    align-items: center;
    justify-content: end;
    padding: 10px;
}

.title {
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 10px;
}

.chat-panel {
    max-height: calc(100vh - 20px);
    border: 3px solid var(--right-side-bubble);
    background-color: var(--primary);
    border-radius: 10px;
    margin: 10px;

    display: grid;
    grid-template-rows: 1fr 6fr 1fr;
}

.chat-panel > header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    font-size: smaller;
    color: #12e5e5;
    margin: 10px;
    border-bottom: 3px solid var(--right-side-bubble);
}

.title > h1 {
    width: 100%;
    padding: 1px 10px;
    border-radius: 10px;
    background-color: var(--background);
}

.chat-content {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    overflow-x: hidden;
    margin: 0 20px;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* Internet Explorer 10+ */
}

.chat-content::-webkit-scrollbar {
    display: none; /* Hide scrollbar for Chrome, Safari, and Opera */
}

.chat-panel > header button {
    padding: 5px;
}
.chat-panel img {
    height: 25px;
}

@media screen and (max-width: 480px) {
    .chat-panel > header {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
    }
}