.chat-bubble {
    height: fit-content;
    width: fit-content;
    display: flex;
    flex-direction: column;
    max-width: 750px;
    margin: 20px;
    border-radius: 10px;
    font-size: 12px;
    text-align: start;
}
.chat-bubble header, .chat-bubble div {
    height: fit-content;
    padding: 10px;
}

.chat-bubble header {
    color: #fff7f7;
    font-weight: bold;
}

/* Left-side chat bubble with tail */
.left {
    align-self: flex-start;
    background-color: var(--left-side-bubble);
    position: relative;
}

.left::after {
    content: '';
    position: absolute;
    top: 85%;
    left: -30px; 
    border-width: 15px;
    border-style: solid;
    border-color: transparent var(--left-side-bubble) transparent transparent;
    transform: translateY(-85%);
}

/* Right-side chat bubble with tail */
.right {
    align-self: flex-end;
    background-color: var(--right-side-bubble);
    position: relative;
}

.right::after {
    content: '';
    position: absolute;
    top: 20%;
    right: -30px; /* Adjust this value to position the tail */
    border-width: 15px;
    border-style: solid;
    border-color: transparent transparent transparent var(--right-side-bubble);
    transform: translateY(-20%);
}
  

.left header {
    text-align: start;
}
.right header {
    text-align: end;
}