.chat-input {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 20px;
}

input {
    flex: 1;
    border: 1.5px solid var(--right-side-bubble);
    border-radius: 5px;
    padding: 10px;
    margin-right: 10px;
    font-size: 14px;
}

button {
    background-color: var(--right-side-bubble);
    color: #ffffff;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 14px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}
