.input-panel {
    width: fit-content;
    background-color: var(--background);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border: 3px solid var(--right-side-bubble);
}
  
.input-panel input[type="text"] {
    width: 200px;
    padding: 8px;
    margin-right: 8px;
}
  
.input-panel button {
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    background-color: var(--right-side-bubble); /* Example button color */
    color: white;
    border: none;
    margin-right: 8px;
}
  
.input-panel button:last-child {
    background-color: var(--primary);
    margin-right: 0;
}
  
/* Mobile styles */
@media screen and (max-width: 480px) {
    .input-panel {
        width: 80%;
        margin: 20% auto; 
        padding: 15px; 
        border-width: 2px;
    }
  
    .input-panel input[type="text"] {
        width: 100%; /* Make the input field full width */
        margin-bottom: 10px; /* Add space below the input field */
    }
  
    .input-panel button {
        width: 100%;
        margin-bottom: 5px;
    }
}